html {
  height: 100%;
}

body {
  height: 100%;
  overflow-y: hidden;
  display: flex;
  justify-content: center;

  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.playing-area {
    display: flex;
    flex-direction: row;
    height: 100%;
}
.player-card-wrapper {
  display: flex;
  width: 500px;
  height: 60px; }
  .player-card-wrapper > * {
    display: block;
    position: absolute;
    margin: -3em; }

.player-one {
  margin-top: auto;
  margin-bottom: 80px; }

.player-two {
  margin-bottom: auto;
  margin-top: 80px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg); }

.card {
  position: relative;
  margin-left: auto;
  width: 100px;
  height: 150px;
  border-radius: 10px;
  background: #fff;
  box-shadow: 3px 3px 7px rgba(0,0,0,0.3);
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}

.card p {
  text-align: center;
  font: 60px/150px Georgia, Times New Roman, serif;
  margin: 0px;
}

.suitdiamonds:before, .suitdiamonds:after {
  content: "\2666";
  color: #ff0000;
}
   
.suithearts:before, .suithearts:after {
  content: "\2665";
  color: #ff0000;
}
   
.suitclubs:before, .suitclubs:after {
  content: "\2663";
  color: #000;
}
   
.suitspades:before, .suitspades:after {
  content: "\2660";
  color: #000;
}

span[class*='suit']:before {
  position: absolute;
  font-size: 35px;
  left: 5px;
  top: 5px;
}
   
span[class*='suit']:after {
  position: absolute;
  font-size: 35px;
  right: 5px;
  bottom: 5px;
}

.card:hover {
  cursor: pointer;
  zoom: 1.1;
  z-index: 1000;
  box-shadow: 4px 4px 8px rgba(0,0,0,0.3);
}

.back {
  box-sizing: border-box;
  border: 5px solid white;
  background: -webkit-linear-gradient(315deg, royalblue 21px, #d9ecff 22px, #d9ecff 24px, transparent 24px, transparent 67px, #d9ecff 67px, #d9ecff 69px, transparent 69px), 
              -webkit-linear-gradient(225deg, royalblue 21px, #d9ecff 22px, #d9ecff 24px, transparent 24px, transparent 67px, #d9ecff 67px, #d9ecff 69px, transparent 69px)0 64px;
  background: linear-gradient(135deg, royalblue 21px, #d9ecff 22px, #d9ecff 24px, transparent 24px, transparent 67px, #d9ecff 67px, #d9ecff 69px, transparent 69px), 
              linear-gradient(225deg, royalblue 21px, #d9ecff 22px, #d9ecff 24px, transparent 24px, transparent 67px, #d9ecff 67px, #d9ecff 69px, transparent 69px)0 64px;
  background-color: royalblue;
  background-size: 64px 128px;
}

.centre {
  box-shadow: 9px 9px 9px rgba(0,0,0,0.3);
}
.circle-container {
  margin: auto;
  width: 40em;
  height: 40em;
  border-radius: 50%;
  padding: 0;
  list-style: none; }
  .circle-container > * {
    display: block;
    position: absolute;
    top: 42%;
    left: 50%;
    margin: -3em; }

